import React, { Component } from 'react';
import { Modal } from 'react-bootstrap';
import { Link } from 'react-router-dom';
class EditCampaignUrlModal extends Component {
    constructor(props) {
        super(props)
        console.log("EditCampaignUrlModal", props)
        this.state = {
            addRule: false,
            campaignName: props.responseData.name
        };
    }

    onChangename = event => {
        this.setState({ campaignName: event.target.value })

    }


    handleClose = event => this.props.campaignUrlCallback(false)

    onClickCreate = event => this.props.onUpdateUrl(this.state.campaignName)
    render() {
        const { campaignUrlModal, responseData, onUpdateUrl } = this.props
        return <Modal show={campaignUrlModal} onHide={this.handleClose}
            size={'lg'}
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title>Edit Hashtag Campaign URL</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="mx-xl-4 px-2 px-lg-4 mx-0 pt-4">
                    <div className="row mb-4 mb-xl-5">
                        <label className="col-sm-3 text-gray-800 mb-2">Campaign Name <strong className="text-danger">*</strong></label>
                        <div className="col-sm-9">
                            <input type="text" name="name" className={`form-control`} placeholder="Enter Campaign Name…" value={this.state.campaignName} onChange={this.onChangename} />
                        </div>
                    </div>
                    <div className="row mb-4 mb-xl-5">
                        <label className="col-sm-3 text-gray-800 mb-2">Your Unique URL <strong className="text-danger">*</strong></label>
                        <div className="col-sm-9">
                            <div className="input-group">
                                <span className="input-group-text" id="campaignUrl__">`https://test.taggbox.com/embed-widget/lite/</span>
                                <input type="text" className="form-control ps-1" placeholder="Enter Your Unique URL…" aria-label="Enter Your Unique URL…" aria-describedby="campaignUrl__" value={this.state.campaignName} readOnly />
                                <Link to={'#'} target="_blank" className="input-group-text svg-icon svg-icon-5 px-2">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="20" height="20">
                                        <path fill="none" d="M0 0h24v24H0z" />
                                        <path d="M10 6v2H5v11h11v-5h2v6a1 1 0 0 1-1 1H4a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h6zm11-3v9l-3.794-3.793-5.999 6-1.414-1.414 5.999-6L12 3h9z" fill="currentColor" />
                                    </svg>
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer className="p-3">
                <div className="mx-xl-4 px-2 px-lg-4 mx-0">
                    <button type="button" className="btn btn-secondary btn-sm btn-min-w me-3" onClick={this.handleClose}>Cancel</button>
                    <button type="button" className="btn btn-primary btn-sm btn-min-w" onClick={this.onClickCreate}>Update Url</button>
                </div>
            </Modal.Footer>
        </Modal>
    }
}

export default EditCampaignUrlModal;
